.bubble {
  position: fixed;
  right: 50%;
  bottom: -20%;
  border-radius: 50%;
  pointer-events: none;
  animation: animate 5s linear infinite;
  background: #0000;
  box-shadow: inset 0 0 10px #ffffff80;
}

.bubble::before {
  content: "";
  position: fixed;
  width: 100%;
  height: 100%;
  background: radial-gradient(#fff, #0000);
  border-radius: 50%;
  transform: scale(0.25) translate(-70%, -70%);
}

@keyframes animate {
  0% {
    opacity: 1;
    transform: translateY(0);
  }
  99% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    transform: translateY(-1200%);
  }
}
