@keyframes strike {
    100% {
        width: 105%;
    }
  }
  
  @keyframes highlight {
    0% {
        background-color: initial;
    }
    
    100% {
        background-color: auto;
    }
  }

.aboutSection {
    span {
        font-weight: 700;
        @media (max-width: 1400px) {
            font-size: 1rem;
        }
    }

    p {
        margin: 0 auto;
        padding: 20px;
        text-align: center;
        width: 300px;
    }
}

.title {
    align-items: center;
    display: flex;
    justify-content: center;
    margin: 0 auto;

    img {
        width: 225px;

        @media (max-width: 1400px) {
            width: 100px;
        }
    }
}

.font {
    font-family: Water, sans-serif;
    letter-spacing: 3px !important;
    color: #fff;
    font-size: 2.5rem !important;
    font-weight: 700;
    padding: 20px;
    text-align: center;
    font-size: 3rem;
    word-spacing: 15px;

    @media (max-width: 1400px) {
        font-size: 1rem  !important;
        word-spacing: 10px  !important;
    }
}

.aboutText {
    color: #fff;
    font-size: 2.5rem;
    font-weight: 700;
    padding: 20px;
    text-align: center;
    margin: 0 auto;
    width: 1200px;

    @media (max-width: 1400px) {
        width: 350px;
    }
}

.strike {
    position: relative;

    &::after {
        animation: strike 2s linear .3s 1 forwards;
        background: repeat-x url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAB0AAAAKAQMAAAByjsdvAAAABlBMVEUAAADdMzNrjRuKAAAAAXRSTlMAQObYZgAAADdJREFUCNdj+MMABP8ZGCQY/h9g+MHw/AHzDwbGD+w/GBhq6h8wMNj/b2BgkP8HVMMPUsn+gQEAsTkQNRVnI4cAAAAASUVORK5CYII=);
        content: " ";
        height: 10px;
        left: -2%;
        opacity: 80%;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        width: 0;
    }
}

.highlight {
    animation: highlight 4s linear;
    background-color: #ffd465cc;
}

.cat {
    width: 350px;

    @media (max-width: 1400px) {
        width: 200px;
    }
}