.address {
    background-color: #21212199;
    border-radius: 10px;
    margin: 0 auto 20px;
    padding: 1px 5px 5px;
    width: 500px;

    @media (max-width: 1400px) {
        width: 320px;

        span {
            font-size: .7rem;
        }
    }

    span {
        color: #fff !important;
        cursor: pointer;
    }
}

.contractText {
    background-color: #21212180;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    color: #fff;
    margin-left: -.5% !important;
    margin: 0 0 10px;
    padding: 0 0 0 5px;
    text-align: left;
    width: 100.5%;
}

.font {
    color: #fff;
    font-size: 2.5rem;
    font-weight: 700;
    padding: 20px;
    text-align: center;
    font-family: Water, sans-serif;
    letter-spacing: 3px;

    @media (max-width: 1400px) {
        font-size: 1rem !important;
    }
}

.community {
    display: grid;
    justify-content: center;
    padding: 20px;
}

.aboutSection {
    span {
        font-weight: 700;
    }

    p {
        margin: 0 auto;
        padding: 20px;
        text-align: center;
        width: 300px;
    }
}

.title {
    align-items: center;
    display: flex;
    justify-content: center;
    margin: 0 auto;

    img {
        width: 225px;
    }
}

.font {
    font-family: Water, sans-serif;
    letter-spacing: 3px !important;
    color: #fff;
    font-size: 2.5rem;
    font-weight: 700;
    padding: 20px;
    text-align: center;
    font-size: 3rem;
}

.aboutText {
    color: #fff;
    font-size: 2.5rem;
    font-weight: 700;
    padding: 20px;
    text-align: center;
    margin: 0 auto;
    width: 1200px;
}

.strike {
    position: relative;

    &::after {
        animation: strike 2s linear .3s 1 forwards;
        background: repeat-x url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAB0AAAAKAQMAAAByjsdvAAAABlBMVEUAAADdMzNrjRuKAAAAAXRSTlMAQObYZgAAADdJREFUCNdj+MMABP8ZGCQY/h9g+MHw/AHzDwbGD+w/GBhq6h8wMNj/b2BgkP8HVMMPUsn+gQEAsTkQNRVnI4cAAAAASUVORK5CYII=);
        content: " ";
        height: 10px;
        left: -2%;
        opacity: 80%;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        width: 0;
    }
}

.highlight {
    animation: highlight 4s linear;
    background-color: #ffd465cc;
}